import { Component } from 'preact';

import { config } from './cta.config.js'

import CtaTrainView from './ctaTrainView';
import CtaBusView from "./ctaBusView";

import style from './style.scss';

const axios = require('axios');

// Map object representing the CTA defined train line keys and their respective names
const CTA_ROUTES = {
	red: 'Red',
	blue: 'Blue',
	brn: 'Brown',
	g: 'Green',
	org: 'Orange',
	p: 'Purple',
	pink: 'Pink',
	y: 'Yellow'
};

// Loading State Enums and respective styles that represent at which point the CTA Data is being loaded; Loading -> Just started to load, Ready -> Idle state before Refresh is clicked
const LOADING_STATES = Object.freeze({
	'LOADING': 0,
	"JUSTLOADED": 1,
	"LOADED": 2,
	"READY": 3
});
const LOADING_STATES_STYLES = {
	0: style.ctaAppControlsBtnRefreshLoading,
	1: style.ctaAppControlsBtnRefreshJustLoaded,
	2: style.ctaAppControlsBtnRefreshLoaded,
	3: style.ctaAppControlsBtnRefreshReady
};

export default class CtaAppParent extends Component {

	state = {
		showTrains: true,
		stopPredictions: {},
		currentlyLoading: config.STOPS.length,
		loadingState: LOADING_STATES.LOADING
	};

	componentWillMount() {
		// Before the component mounts to the DOM, ensure the stopPredictions object is initialized for each train/bus stop
		this.initStopPredictions();
	}

	componentDidMount() {
		// On the initial component mount to the DOM, refresh and get all stop times
		this.refreshTimes();
	}

	// Initialize the key/value pair object that contains the corresponding time predictions for all train/bus stops
	initStopPredictions = () => {
		let stopPredictions = {};

		// Iterate through each train/bus stop and create the baseline prediction object
		config.STOPS.forEach((stop, index) => {
			stopPredictions[index] = {
				isTrain: stop.isTrain,
				predictions: [],
				stationName: '',
				direction: '',
				destination: '',
				error: ''
			};
		});

		this.setState({
			stopPredictions: stopPredictions
		});
	};

	// Refresh/update/get all train and bus stop prediction times
	refreshTimes = () => {

		// Ensure the Loading State is reset to "Loading" and we're currently loading all CTA stops in the config
		this.setState({
			currentlyLoading: config.STOPS.length,
			loadingState: LOADING_STATES.LOADING
		});

		// Iterate through each train/bus stop, asynchronously getting the next prediction times and setting the state
		config.STOPS.forEach((stop, index) => {
			if(stop.isTrain){
				this.getNextTrainTransit(index, stop.stopId, stop.route);
			}else{
				this.getNextBusTransit(index, stop.stopId, stop.route);
			}
		});

	};

	// Given the stop's key/index, stop ID, and train route, ensure the setstate updates the stop prediction for this respective train stop
	getNextTrainTransit = async (key, stopId, route) => {

		let stopPrediction = {};

		// TODO REFACTOR THESE TWO FUNCTIONS INTO SEPARATE FILE
		// Given a prdt and a arrT, return an integer representing the minutes difference between the two timestamps
		const getDifferenceInMinutes = (arrT, prdt) => {
			return ((new Date(arrT)) - (new Date(prdt))) / 60000;
		};
		// Given the raw countdown (in minutes) from the CTA API, return the modified/filtered string for display
		const getModifiedCountdown = countdown => {
			if(isNaN(countdown)){
				// TODO ERROR
			}else if(countdown <= 1){
				return "Due";
			}else{
				return countdown + " min";
			}
		};

		let response = await axios.get(`${config.PROXY_URL}${config.TRAIN_ROOT_URL}api/1.0/ttarrivals.aspx?key=${config.TRAIN_API_KEY}&stpid=${stopId}&outputType=JSON`);

		if(response && response.data && response.data.ctatt){

			if(response.data.ctatt.eta && 0 < response.data.ctatt.eta.length){

				// Filter on the given route predictions to ensure all predictions belong to the current route/train line focused on
				let predictions = response.data.ctatt.eta.filter(prediction => prediction.rt.toLowerCase() === route.toLowerCase());
				if(0 < predictions.length){

					let predictionValues = [];
					predictions.forEach((prediction) => {
						predictionValues.push(getModifiedCountdown(getDifferenceInMinutes(prediction.arrT, prediction.prdt)));
					});

					stopPrediction = {
						stationName: predictions[0].staNm,
						destination: predictions[0].destNm,
						predictions: predictionValues
					};

				}else{
					// The CTA Train Stop has predictions for other train routes, but not this train route
					stopPrediction = {
						error: (CTA_ROUTES[route.toLowerCase()] + " Line: No arrival times")
					};
				}

			}else if (response.data.ctatt.errNm && 0 < response.data.ctatt.errNm.length){
				stopPrediction = {
					error: (CTA_ROUTES[route.toLowerCase()] + " Line: " + response.data.ctatt.errNm)
				};
			}else{
				// TODO ERROR
			}

		}else{
			// TODO ERROR
		}

		this.addStopPrediction(key, stopPrediction);

	};

	// Given the stop's key/index, stop ID, and bus route, ensure the setstate updates the stop prediction for this respective bus stop
	getNextBusTransit = async (key, stopId, route) => {

		let stopPrediction = {};

		// TODO REFACTOR THESE TWO FUNCTIONS INTO SEPARATE FILE
		// Given the raw direction from the CTA API, return the modified/filtered string for display
		const getModifiedDirection = direction => {
			return direction.replace('bound', '');
		};
		// Given the raw countdown string from the CTA API, return the modified/filtered string for display
		const getModifiedCountdown = countdown => {
			if(isNaN(countdown)){
				if(countdown.toUpperCase().includes('DUE')){
					return 'Due';
				}else if(countdown.toUpperCase().includes('DLY')){
					return 'Delayed';
				}else{
					return countdown;
				}
			}else{
				return countdown + " min"
			}
		};

		let response = await axios.get(`${config.PROXY_URL}${config.BUS_ROOT_URL}bustime/api/v2/getpredictions?key=${config.BUS_API_KEY}&format=json&rt=${route}&stpid=${stopId}`);

		// TODO Handle response.data['bustime-response'].error -> "msg": "No arrival times"
		if(response && response.data && response.data['bustime-response']){
			if(response.data['bustime-response'].prd && 0 < response.data['bustime-response'].prd.length){

				let predictionValues = [];
				let predictions = response.data['bustime-response'].prd;
				predictions.forEach((prediction, index) => {
					predictionValues.push(getModifiedCountdown(prediction.prdctdn));
				});

				stopPrediction = {
					direction: getModifiedDirection(response.data['bustime-response'].prd[0].rtdir),
					destination: response.data['bustime-response'].prd[0].des,
					predictions: predictionValues
				};

			}else if (response.data['bustime-response'].error && 0 < response.data['bustime-response'].error.length){
				stopPrediction = {
					error: (route + ": " + response.data['bustime-response'].error[0].msg)
				};
			}else{
				// TODO ERROR
			}
		}else{
			// TODO ERROR
		}

		this.addStopPrediction(key, stopPrediction);

	};

	// Given the key/index and the stop prediction object representing the given stop, ensure the state is updated respectively
	addStopPrediction = (key, stopPrediction) => {

		// If this is the final CTA stop being loaded, ensure the transition to the next state is initialized
		if((this.state.currentlyLoading - 1) <= 0){
			this.initLoadingTransition();
		}

		const stopPredictions = { ...this.state.stopPredictions, [key]: stopPrediction};
		this.setState((prev) => ({
			stopPredictions: stopPredictions,
			currentlyLoading: prev.currentlyLoading - 1
		}));

	};

	// Show the CTA Train cards
	showTrains = () => {
		this.setState({showTrains: true});
	};

	// Show the CTA Bus cards
	showBuses = () => {
		this.setState({showTrains: false});
	};

	// Initialize the transition from loading through to the idle state of 'ready'
	initLoadingTransition = () => {
		setTimeout(() => {
			this.setState({loadingState: LOADING_STATES.JUSTLOADED});
			setTimeout(() => {
				this.setState({loadingState: LOADING_STATES.LOADED});
				setTimeout(() => {
					this.setState({loadingState: LOADING_STATES.READY});
				}, 550);
			}, 5000);
		}, 550);
	};

	render() {

		let trainStops = [];
		let busStops = [];
		config.STOPS.forEach((stop, index) => {
			if(stop.isTrain){
				trainStops.push(<CtaTrainView stopPrediction={this.state.stopPredictions[index]} ctaRoute={CTA_ROUTES[stop.route.toLowerCase()]} />);
			}else{
				busStops.push(<CtaBusView stopPrediction={this.state.stopPredictions[index]} route={stop.route} stopId={stop.stopId} />);
			}
		});

		let loadingProgressPercent = ((config.STOPS.length - this.state.currentlyLoading) / config.STOPS.length) * 100;

		return (
			<div className={[style.ctaApp, (this.state.showTrains ? style.ctaAppShowTrains : style.ctaAppShowBuses)].join(' ')}>

				<div className={style.ctaAppControls}>

					<button onClick={this.showTrains} role={'button'} aria-label={'Show Train Times'} aria-pressed={this.state.showTrains} className={[style.ctaAppControlsBtn, style.ctaAppControlsBtnTrain, (this.state.showTrains ? style.ctaAppControlsBtnActive : '')].join(' ')}>Show Trains</button>

					<button onClick={this.showBuses} role={'button'} aria-label={'Show Bus Times'} aria-pressed={!this.state.showTrains} className={[style.ctaAppControlsBtn, style.ctaAppControlsBtnBus, (this.state.showTrains ? '' : style.ctaAppControlsBtnActive)].join(' ')}>Show Buses</button>

					<button onClick={this.refreshTimes} role={'button'} aria-label={'Refresh'} className={[style.ctaAppControlsBtn, style.ctaAppControlsBtnRefresh, LOADING_STATES_STYLES[this.state.loadingState]].join(' ')}>
						<span className={style.ctaAppControlsBtnRefreshText}>Refresh</span>
						<span className={style.ctaAppControlsBtnRefreshProgress} style={{width: (this.state.loadingState === LOADING_STATES.READY ? 0 : loadingProgressPercent + '%')}}></span>
					</button>

				</div>

				<div className={[style.ctaAppStops, style.ctaAppStopsTrains].join(' ')}>
					{trainStops}
				</div>

				<div className={[style.ctaAppStops, style.ctaAppStopsBuses].join(' ')}>
					{busStops}
				</div>

			</div>
		);
	}
}
