import style from "./style.scss";

const CtaBusView = (props) => {

	if(props.stopPrediction && props.stopPrediction.predictions && 0 < props.stopPrediction.predictions.length){

		let predictionsStr = '';
		props.stopPrediction.predictions.forEach((prediction, index) => {
			if(index !== 0){
				predictionsStr += prediction;
				if(index !== (props.stopPrediction.predictions.length - 1)){
					predictionsStr += ', ';
				}
			}
		});

		return (
			<div className={[style.ctaAppChild, style.ctaAppChildBus].join(' ')}>
				<div className={style.ctaAppChildLeft}>
					<div className={style.ctaAppChildLarge}>
						{props.route} to {props.stopPrediction.destination}
					</div>
					<div className={style.ctaAppChildSmall}>
						{props.stopPrediction.direction}
					</div>
				</div>
				<div className={style.ctaAppChildRight}>
					<div className={style.ctaAppChildLarge}>
						{props.stopPrediction.predictions[0]}
					</div>
					<div className={style.ctaAppChildSmall}>
						{predictionsStr}
					</div>
				</div>
			</div>
		);
	}else if (props.stopPrediction && props.stopPrediction.error && props.stopPrediction.error && 0 < props.stopPrediction.error.length){
		return (
			<div className={[style.ctaAppChild, style.ctaAppChildBus].join(' ')}>
				<div className={style.ctaAppChildLeft}>
					<div className={style.ctaAppChildLarge}>
						{props.stopPrediction.error}
					</div>
				</div>
			</div>
		);
	}else {
		return (
			<div className={[style.ctaAppChild, style.ctaAppChildBus].join(' ')}>
				<div className={style.ctaAppChildLeft}>
					<div className={style.ctaAppChildLarge}>
						Loading {props.route} at {props.stopId}
					</div>
				</div>
			</div>
		);
	}
	
};

export default CtaBusView;
